.checkout-container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  padding: 2rem;
  border-radius: 8px;
}

.checkout-container h2 {
  margin-bottom: 2rem;
  color: #2c3e50;
}

.checkout-section {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #eee;
}

.checkout-section:last-child {
  border-bottom: none;
}

.checkout-section h3 {
  margin-bottom: 1rem;
  color: #2c3e50;
}

/*    */
.section-note {
  background: linear-gradient(135deg, #fff3cd 0%, #ffeeba 100%);
  color: #856404;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  margin-bottom: 1rem;
  font-size: 0.95rem;
  border-left: 4px solid #f0ad4e;
}

.input-group {
  display: flex;
  flex-direction: column;
}

.input-group label {
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #2c3e50;
  font-size: 0.95rem;
}

.input-group input {
  padding: 0.85rem;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.input-group input:focus {
  outline: none;
  border-color: #8e44ad;
  box-shadow: 0 0 0 3px rgba(142, 68, 173, 0.1);
}

.phone-warning {
  background: linear-gradient(135deg, #f8d7da 0%, #f5c6cb 100%);
  color: #721c24;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  margin-top: 0.75rem;
  font-size: 0.9rem;
  font-weight: 500;
  border-left: 4px solid #dc3545;
  text-align: center;
}

/*     */
.custom-order-toggle {
  margin-bottom: 1rem;
}

.toggle-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem;
  background: linear-gradient(135deg, #e8f4fd 0%, #d1ecf1 100%);
  border: 2px solid #bee5eb;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-label:hover {
  border-color: #8e44ad;
  background: linear-gradient(135deg, #f3e5f5 0%, #e1bee7 100%);
}

.toggle-label input[type="checkbox"] {
  width: 20px;
  height: 20px;
  cursor: pointer;
  accent-color: #8e44ad;
}

.toggle-text {
  font-size: 1rem;
  font-weight: 600;
  color: #2c3e50;
}

.custom-order-form {
  margin-top: 1rem;
  padding: 1.5rem;
  background: linear-gradient(135deg, #f8f9ff 0%, #eef2ff 100%);
  border-radius: 8px;
  border: 2px solid #c5cae9;
  animation: slideIn 0.3s ease-out;
}

.custom-order-note {
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  color: #2e7d32;
  padding: 1rem;
  border-radius: 6px;
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
  border-left: 4px solid #4caf50;
}

.custom-order-form .input-group {
  margin-bottom: 1.25rem;
}

.custom-order-form textarea {
  width: 100%;
  padding: 0.85rem;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  font-family: inherit;
  resize: vertical;
  transition: all 0.3s ease;
}

.custom-order-form textarea:focus {
  outline: none;
  border-color: #8e44ad;
  box-shadow: 0 0 0 3px rgba(142, 68, 173, 0.1);
}

.custom-order-form input[type="date"] {
  width: 100%;
  padding: 0.85rem;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.custom-order-form input[type="date"]:focus {
  outline: none;
  border-color: #8e44ad;
  box-shadow: 0 0 0 3px rgba(142, 68, 173, 0.1);
}

.custom-order-warning {
  background: linear-gradient(135deg, #fff3cd 0%, #ffeeba 100%);
  color: #856404;
  padding: 1rem;
  border-radius: 6px;
  margin-top: 1rem;
  font-size: 0.9rem;
  border-left: 4px solid #f0ad4e;
}

.custom-order-warning p {
  margin: 0;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}

.form-row input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

.payment-options {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.payment-option {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  padding: 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.payment-option:hover {
  border-color: #8e44ad;
  background-color: #f9f9f9;
}

.payment-option input[type="radio"] {
  margin-top: 0.25rem;
  cursor: pointer;
}

.payment-option input[type="radio"]:checked + .payment-label {
  color: #8e44ad;
  font-weight: 600;
}

.payment-label {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  flex: 1;
}

.payment-description {
  font-size: 0.85rem;
  color: #666;
  display: block;
}

.card-details-form {
  margin-top: 1.5rem;
  padding: 1.5rem;
  background: linear-gradient(135deg, #f5f7fa 0%, #e8eaf6 100%);
  border-radius: 8px;
  border-left: 4px solid #8e44ad;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.card-details-form h4 {
  margin-top: 0;
  margin-bottom: 1.5rem;
  color: #2c3e50;
  font-size: 1.1rem;
}

.card-form-group {
  margin-bottom: 1.25rem;
}

.card-form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #2c3e50;
  font-weight: 500;
  font-size: 0.95rem;
}

.card-input {
  width: 100%;
  padding: 0.85rem;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  font-family: 'Courier New', monospace;
  transition: all 0.3s ease;
  background: white;
}

.card-input:focus {
  outline: none;
  border-color: #8e44ad;
  box-shadow: 0 0 0 3px rgba(142, 68, 173, 0.1);
}

.card-form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.card-note {
  margin-top: 1rem;
  padding: 0.75rem;
  background: rgba(46, 213, 115, 0.1);
  border: 1px solid rgba(46, 213, 115, 0.3);
  border-radius: 6px;
  color: #27ae60;
  font-size: 0.9rem;
  text-align: center;
}

.cod-info {
  margin-top: 1.5rem;
  padding: 1.5rem;
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  border-radius: 8px;
  border-left: 4px solid #27ae60;
  animation: slideIn 0.3s ease-out;
}

.cod-note {
  margin: 0;
  color: #2c3e50;
  font-size: 1rem;
  font-weight: 500;
}

.order-summary {
  background-color: #f8f9fa;
  padding: 1.5rem;
  border-radius: 4px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.75rem;
}

.summary-total {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 2px solid #ddd;
  font-size: 1.3rem;
  font-weight: bold;
}

.place-order-btn {
  width: 100%;
  background-color: #27ae60;
  color: white;
  border: none;
  padding: 1rem;
  border-radius: 4px;
  font-size: 1.1rem;
  cursor: pointer;
  margin-top: 2rem;
}

.place-order-btn:hover:not(:disabled) {
  background-color: #229954;
}

.place-order-btn:disabled {
  background-color: #95a5a6;
  cursor: not-allowed;
}

@media (max-width: 1024px) {
  .checkout-container {
    max-width: 90%;
  }
}

@media (max-width: 768px) {
  .checkout-container {
    padding: 1.5rem 1rem;
    margin: 1rem auto;
  }

  .checkout-container h2 {
    font-size: 1.75rem;
    margin-bottom: 1.5rem;
  }

  .checkout-section h3 {
    font-size: 1.2rem;
  }

  .form-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }

  .card-form-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }

  .payment-option {
    padding: 0.875rem;
  }

  .card-details-form,
  .cod-info {
    padding: 1.25rem;
  }

  .order-summary {
    padding: 1.25rem;
  }

  .summary-total {
    font-size: 1.2rem;
  }

  .place-order-btn {
    padding: 0.875rem;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .checkout-container {
    padding: 1rem 0.75rem;
    margin: 0.5rem auto;
  }

  .checkout-container h2 {
    font-size: 1.5rem;
    margin-bottom: 1.25rem;
  }

  .checkout-section {
    margin-bottom: 1.5rem;
    padding-bottom: 1.5rem;
  }

  .checkout-section h3 {
    font-size: 1.1rem;
    margin-bottom: 0.875rem;
  }

  .form-row input,
  .card-input {
    padding: 0.65rem;
    font-size: 0.95rem;
  }

  .payment-option {
    padding: 0.75rem;
    gap: 0.5rem;
  }

  .payment-label {
    font-size: 0.95rem;
  }

  .payment-description {
    font-size: 0.8rem;
  }

  .card-details-form,
  .cod-info {
    padding: 1rem;
    margin-top: 1rem;
  }

  .card-details-form h4 {
    font-size: 1rem;
    margin-bottom: 1rem;
  }

  .card-form-group {
    margin-bottom: 1rem;
  }

  .card-form-group label {
    font-size: 0.9rem;
    margin-bottom: 0.4rem;
  }

  .card-note,
  .cod-note {
    font-size: 0.85rem;
  }

  .order-summary {
    padding: 1rem;
  }

  .summary-row {
    font-size: 0.95rem;
  }

  .summary-total {
    font-size: 1.1rem;
  }

  .place-order-btn {
    padding: 0.75rem;
    font-size: 0.95rem;
    margin-top: 1.5rem;
  }
}
